
.Checkbox-with-describe_wrap{
    width: 266px;
    z-index: 9;
}

.Checkbox-with-describe_div{
    margin-bottom: 15px;
}
.Checkbox-with-describe_cont input[type="checkbox"]{
    display: none;
    & + label{
        background-color: white;
        font-family: 'Montserrat';
        font-size: 14px;
        font-style: normal;
        font-weight: 400;
        line-height: 18px;
        letter-spacing: 0em;
        text-align: left;
        color: rgba(31, 32, 65, 0.5);
        & div{
            display: inline-block;
        }
        &::before {
            content:'';
            display: inline-block;
            margin-right: 10px;
            position: relative;
            text-align: center;
            text-indent: 0px;
            width: 20px;
            height: 20px;
            border: 1px solid rgba(31, 32, 65, 0.25);
            box-sizing: border-box;
            border-radius: 4px;
            border-image: initial;
            vertical-align: top;
            cursor: pointer;
        }

    }

}
.Checkbox-with-describe_cont input[type="checkbox"]:checked{
   
    & + label{
        &::before {
            content: "expand_more";
            font-family: 'Material Icons';
            font-size: 18px;
            background: linear-gradient(180deg, #BC9CFF 0%, #8BA4F9 100%);
            -webkit-background-clip: text;
            -webkit-text-fill-color: transparent;
            color: #BC9CFF;
            border: 1px solid #BC9CFF ;
            }

    }

}
.Checkbox-with-describe-text{
    vertical-align: top;
    & p{
        margin-top: 0px;
        margin-bottom: 5px;
        font-family: 'Montserrat';
    }
    & .Checkbox-with-describe-title{
        font-weight: 700;
        font-size: 14px;
        line-height: 18px;
        color: rgba(31, 32, 65, 0.75);
    }
    & .Checkbox-with-describe-subscribe{
        font-weight: 400;
        font-size: 12px;
        line-height: 14px;
        color: rgba(31, 32, 65, 0.5);
        max-width: 210px;
    }
}
